<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="产品编码">
          <el-input
            style="width: 500px"
            ref="firstInput"
            v-model="listQuery.finished_product_code"
            @input="searcgTable"
          ></el-input>
        </el-form-item>

        <!-- <el-form-item label="生产计划">
          <el-select v-model="listQuery.product_plan_code" placeholder="请选择">
            <el-option
              v-for="(item, index) in ProductPlanCodeNameList"
              :key="index + ' search'"
              :label="item.product_plan_name"
              :value="item.product_plan_code"
            ></el-option>
          </el-select>
        </el-form-item> -->

        <!-- <el-form-item label="物料编码">
          <el-input v-model="listQuery.matter_id"></el-input>
        </el-form-item> -->
        <!-- 查询按钮 -->
        <!-- <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          > -->
        <el-form-item style="margin-left: 20px !important">
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20" v-show="listQuery.finished_product_code">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>过站查询</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline"> </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="产品名称 :" prop="product_name">
                <span>{{ dialogForm.product_name }}</span>
              </el-form-item>
              <el-form-item label="包装编码 :" prop="pack_id">
                <span>{{ dialogForm.pack_id }}</span>
              </el-form-item>
              <el-form-item label="状态 :" prop="status">
                <span>{{ dialogForm.status }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="规格型号 :" prop="rule">
                <span>{{ dialogForm.rule }}</span>
              </el-form-item>
              <el-form-item label="产品编码 :" prop="finished_product_code">
                <span>{{ dialogForm.finished_product_code }}</span>
              </el-form-item>
              <el-form-item label="时间 :" prop="enter_time">
                <span>{{ dialogForm.enter_time }}</span>
              </el-form-item>
            </el-col>
          </el-row>

          <p class="dialog-title">物料明细</p>

          <el-table
            :data="dialogForm.tableData"
            style="width: 100%"
            class="dialogTable"
            height="300"
          >
            <el-table-column prop="matter_name" label="物料名称" align="center">
            </el-table-column>
            <el-table-column prop="matter_id" label="物料编码" align="center">
            </el-table-column>
            <el-table-column prop="rule" label="规格型号" align="center">
            </el-table-column>
            <el-table-column prop="matter_category" label="类型" align="center">
            </el-table-column>
            <el-table-column
              prop="product_time"
              label="入库时间"
              align="center"
            >
            </el-table-column>
            <el-table-column prop="work_name" label="经过工站" align="center">
            </el-table-column>
            <el-table-column prop="user_name" label="过站人员" align="center">
            </el-table-column>
            <el-table-column prop="out_time" label="出站时间" align="center">
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MatterProductSearch",
  components: {
    ELTable,
  },

  computed: {
    ...mapState([
      "ProductionPlanCode",
      "ProductPlanCodeNameList",
      "MatterCodeNameList",
      "BomMatterCodeNameList",
      "PersonCodeNameList",
      "CurrentUserInfo",
      "WorkCodeNameList",
    ]),
  },
  data() {
    return {
      dialogFlag: false,
      timeout: "",
      // 筛选
      listQuery: {
        finished_product_code: "",
        matter_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        finished_product_code: "",
        matter_id: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      dialogForm: {
        product_name: "",
        prodect_code: "",
        finished_product_code: "",
        status: "",
        rule: "",
        pack_id: "",
        enter_time: "",
        tableData: [],
        response_datas: [],
      },
      dialogFormBase: {
        product_name: "",
        prodect_code: "",
        finished_product_code: "",
        status: "",
        rule: "",
        pack_id: "",
        enter_time: "",
        tableData: [],
        response_datas: [],
      },
      ispagination: true, // 分页是否显示
      // 表格列
      tableCol: [
        // {
        //   label: "生产计划",
        //   field: "product_plan_name",
        //   align: "center",
        // },
        // {
        //   label: "产品名称",
        //   field: "product_name",
        //   align: "center",
        // },
        {
          label: "产品编码",
          field: "finished_product_code",
          align: "center",
        },

        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },

        {
          label: "检验结果",
          field: "test_result",
          align: "center",
          width: "200px",
          colType: "tag",
          tagRender: (row) => {
            if (row.test_result == "PASS") {
              row.test_result = "合格";
            } else if (row.test_result == "FAIL") {
              row.test_result = "不合格";
            }
            let params = {
              tagType: "",
              tagText: row.test_result,
            };
            if (row.test_result == "不合格") {
              params.tagType = "warning";
            } else if (row.test_result == "入站" || row.test_result == "出站") {
              params.tagType = "success";
            } else {
              params.tagType = "";
            }
            return params;
          },
        },
        {
          label: "操作人员",
          field: "user_name",
          width: "200px",
          align: "center",
        },

        {
          label: "时间",
          field: "time",
          align: "center",
        },

        // {
        //   label: "操作",
        //   colType: "solt",
        //   align: "center",
        //   option: [
        //     {
        //       label: "详情",
        //       event: this.colDetailBtn,
        //     },
        //   ],
        // },
      ],
      tableData: [],
      NoPageMatterData: [],
    };
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanDeal",
      "getLogisticProductPlanCodeName",
      "getLogisticMatterNameCode",
      "getLogisticBomMatterCodeName",
      "getLogisticPersonCodeName",
      "getLogisticWorkCodeName",
    ]),
    // 通过产品编码 查询 物料
    getLogisticNoPageMatterSearch() {
      baseManagement.getLogisticNoPageMatterSearch().then((res) => {
        console.log("通过产品编码 查询 物料", res);
        if (res.code == 0) {
          // let resData = res.data;
          // this.form.response_datas = res.data;
          this.NoPageMatterData = res.data.data;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    colDetailBtn(rowData) {
      console.log(rowData);
      let params={
        finished_product_code:rowData.finished_product_code,
        work_code:rowData.work_code,
      }
      baseManagement.getLogisticOneProductLiveDeal(params).then((res) => {
        console.log("生命周期详情", res);
        if (res.code == 0) {
          
          
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // colDetailBtn(rowData) {
    //   console.log(rowData);
    //   this.dialogTitle = "产品详情";
    //   this.dialogFlag = true;
    //   let dialogTbaleData = [];
    //   rowData.response_datas.forEach((item) => {
    //     console.log('123213',item);
    //     console.log('435654',this.NoPageMatterData);
    //     this.NoPageMatterData.forEach((NoPageMatterDataItem) => {
    //       if (item == NoPageMatterDataItem.matter_id) {
    //         this.PersonCodeNameList.forEach((CodeNameListItem) => {
    //           if (NoPageMatterDataItem.user_code == CodeNameListItem.user_code) {
    //             NoPageMatterDataItem.user_name = CodeNameListItem.user_name;
    //           }
    //         });
    //         dialogTbaleData.push(NoPageMatterDataItem);
    //       }
    //     });
    //   });
    //   console.log("dialogTbaleData", dialogTbaleData);

    //   this.dialogForm = {
    //     prodect_code: rowData.prodect_code,
    //     product_id: rowData.product_id,
    //     pack_id: rowData.pack_id,
    //     enter_time: rowData.enter_time,
    //     product_name: rowData.product_name,
    //     rule: rowData.rule,
    //     finished_product_code: rowData.finished_product_code,
    //     status: rowData.status,
    //     tableData: dialogTbaleData,
    //     response_datas: rowData.response_datas,
    //   };
    // },
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      // this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
      this.tableData = [];
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
    },
    // 过站查询 查询
    async getLogisticOneProductInAllWorkStationInfo(params) {
      baseManagement
        .getLogisticOneProductInAllWorkStationInfo(params)
        .then((res) => {
          console.log("过站查询 查询", res);
          if (res.code == 0) {
            let tableData = res.data.data;
            tableData.forEach((tableDataItem) => {
              this.PersonCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.user_code == CodeNameListItem.user_code) {
                  tableDataItem.user_name = CodeNameListItem.user_name;
                }
              });
              this.WorkCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.work_code == CodeNameListItem.work_code) {
                  tableDataItem.work_name = CodeNameListItem.work_name;
                }
              });
            });
            this.tableData = tableData;
            this.listQuery.total = res.data.total;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    searcgTable() {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (this.listQuery.finished_product_code) {
          this.listQuery.page = 1;
          this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
        }
      }, 500);
    },
  },
  mounted() {
    this.getLogisticPersonCodeName();
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();
    // 生产计划内码和名称接口 查询
    this.getLogisticProductPlanCodeName();
    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 物料内码和物料名称 查询
    this.getLogisticMatterNameCode();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 过站查询 查询
    // this.getLogisticOneProductInAllWorkStationInfo(this.listQuery);
    // 通过产品编码 查询 物料
    this.getLogisticNoPageMatterSearch();
    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

